@import '../App.scss';

.display-card{
display: flex;
flex-direction: column;
margin: 1rem auto;
box-shadow:0 3px 7px -1px rgba(248, 22, 22, 0.1); 
margin-bottom: 1.6%;
background: rgb(0, 0, 0);
line-height: 1.4;
font-family:starwars,'Krona One', sans-serif;
color: yellow;
border-radius: 5px;
overflow: hidden;
z-index: 0;
}

.display-card button {
  color: rgb(5, 5, 39);
  font-family:starwars,'Krona One', sans-serif;
}

div .photo{
  background: url(../assets/img/planet-1.jpg);
  width: 300px;
  height: 300px;
  margin-bottom: 10px;
}

.display-card .description .readmore button:hover{
    color: rgb(143, 25, 25);
    transform: skew(-3deg);
}
.photo:hover {
  transform: scale(1.1);
}
.display-card .meta{
    position: relative;
    z-index: 0;
    height: 200px;
}
.display-card .photo{
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-size: cover;
  background-position: center;
  transition: transform 0.2s;
}
.display-card .description{
  padding: 1rem;
  background: rgb(12, 1, 1);
  position: relative;
  z-index: 1;

}
.display-card .description h1{
  line-height: 1;
  margin: 0;
  font-size: 1.7rem;
}
.display-card .description .readmore  {
    text-align: right;
    font-size: 1rem;
    letter-spacing: 1.5px;

       button {
        color: #000000;
        display: inline-block;
        position: relative;
        font-size:1.1rem;
        font-family:starwars,'Krona One', sans-serif;
        text-decoration: none;
      }
      
}


